<template>
    <div class="header">
            <div class="background">
                <img :src="seller.avatar" class="avatar1"> 
            </div>   
            <div class="headTop">       
                <div class="topLeft">
                    <img :src="seller.avatar" class="avatar2" >         
                </div>
                <div class="topMiddle">
                    <img src="/static/imgs/brand.png" class="brand">  
                    <span  class="title1">{{seller.name}}</span> 
                    <p  class="title2">{{seller.description}}</p> 
                    <img src="/static/imgs/decrease.png" class="decrease">
                    <span v-if="seller.supports" class="title3">{{seller.supports[0].description}}</span>                   
                </div>
                <div class="topRight" @click="show">
                    <p class="title4" >5个></p> 
                </div>
            </div>       
            <div class="headBotoom">
                <img src="/static/imgs/bulletin.png" class="bulletin">
                <span class="title5">{{seller.bulletin}}</span>
                <span class="title6">></span>
            </div>      
           <Detail></Detail>
     </div>  
</template>

<script>
    import {mapGetters} from 'vuex'
    import Detail from '../detail/detail.vue'

    export default{
        computed:{
            ...mapGetters([
                'seller',        
            ])
        },
        components:{
           Detail,
        },
        created(){
            this.$store.dispatch('getseller');
        },
        methods:{
            show(){
                this.$store.dispatch('getdetail')
            }
        }
    }
    
</script>
<style lang='less' scoped>
    .header{
        width:100%;     
        height:2.68rem;
        background-color:rgba(7,17,27,0.5);
        position: relative;
        color:rgb(255,255,255);
        font-weight: 200;
        font-size:0.2rem;
        overflow: hidden;
        .background{
            position: absolute;
            left:0;
            right:0;
            top:0; 
            bottom:0;
            z-index:-99;
            .avatar1{
                width: 100%; 
                height: 100%;       
                -webkit-filter: blur(0.1rem); /* Chrome, Opera */
                -moz-filter: blur(0.1rem);        
            }  
        }     
       .headTop{
           display: flex;
           -webkit-flex-direction: row;
           position: absolute;
           top:0;
           .topLeft{
            width: 2.08rem;
            height: 1.76rem;
               .avatar2{
                    width: 1.28rem;
                    height: 1.28rem;
                    border-radius: 0.04rem;
                    margin-top:0.48rem;
                    margin-left:0.48rem;         
                }
            }
            .topMiddle{
                height: 1.24rem; 
                margin-top:0.52rem;
                .brand{
                    height: 0.36rem;
                    vertical-align: top;
                }      
                .title1{              
                    font-size:0.32rem;
                    line-height: 0.36rem;
                    margin-left:0.12rem;
                }
                .title2{
                    margin-top:0.16rem;
                    margin-bottom: 0.2rem;
                    font-size:0.24rem;
                }
                .decrease{
                    height: 0.24rem;
                    vertical-align: bottom;               
                }
                .title3{
                line-height: 0.24rem;
                }      
            }
            .topRight{
                height: 0.6rem;
                width: 0.9rem;
                border-radius: 0.25rem;
                background-color:rgba(0,0,0,0.2);
                text-align: center;
                position:absolute; 
                top:1.28rem;           
                right:-1.5rem;
                .title4{    
                    display: inline-block; 
                    line-height: 0.6rem;                          
                }
            }
       }
       .headBotoom{
           position: absolute;
           bottom:0;
           width: 100%;
           height: 0.56rem;
           background-color:rgba(7,17,27,0.2);
           display: flex;
           -webkit-flex-direction: row;               
           .bulletin{
             height: 0.24rem;         
             padding-left:0.24rem;         
             margin:auto;         
           }
           .title5{
               line-height: 0.56rem;
               margin-left: 0.08rem;
               display: inline-block;
               overflow: hidden;
               white-space: nowrap;
               text-overflow: ellipsis;         
           }
           .title6{
               line-height: 0.56rem;
               margin-left: 0.08rem;
               margin-right:0.24rem;           
           }
       }       
    }
</style>



