<template>
	<div>
		<div class="goods">
			<div class="menu-wrapper" ref = "menuWrapper">  
				<div class="goods-menu"> 
					<ul class="menuUl">
						<li  @click='selectMenu(index,$event)' v-for="(v,index) in goods" :key="index" class="menuLi"  :class = "{'current' : index === currentIndex}">
							<span class="menuSpan">{{v.name}}</span>
						</li>
					</ul>
				</div> 
			</div>  
			<div class="foods-wrapper" ref = "foodsWrapper">  
				<div class="goods-foods">	
					<div  v-for="(items,k) in goods" :key="k" class="food-list-hook">					
						<span  class="foodsTitle">{{items.name}}</span> 
						<div class="foodsList" v-for="(item,i) in items.foods" :key="i"  @click="showFoods(item,$event)">
							<div class="foods-content"> 
								<img class="foodsImg" :src='item.image'>
								<div class="foodsText">
									<p class="text1">{{item.name}}</p>
									<p class="text2" v-if="item.description">{{item.description}}</p>     
									<p class="text34">
										<span class="text3"> 月销售量{{item.sellCount}}</span>
										<span class="text4">好评率{{item.rating}}%</span>			 
									</p>
									<P class="textlast">
										<p class="text56">
											<span class="text5" >￥{{item.price}}</span>
											<span class="text6" v-show="item.oldPrice">￥{{item.oldPrice}}</span> 
										</p>
								        <p class="text7"><Addcut :food="item"></Addcut></p> 							   
									</P>  
								</div>	
							</div>	
						</div>			 
					</div>		
				</div> 
			</div> 
		</div> 
		<Foods></Foods>		  
	</div> 
</template>

<script>
	import {mapGetters} from 'vuex'
	import BScroll from 'better-scroll'
	import Foods from '../foods/foods.vue'
	import Addcut from '../AddCut/addcut.vue'
	   
	export default {
		data(){
          return{
			  listHeight:[],
			  scrollY:0
		  }
		},
		components : {
			 Foods,	
			 Addcut					    
        },
		computed:{
            ...mapGetters([
                'goods',        
            ]),
		   currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    }
        },
        created(){
			this.$store.dispatch('getgoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},100)
			})
		},
		methods:{
			//点击进入食物详情页的click事件设置；
			showFoods(item,e){
				if(!e._constructed){
					return;
				}
				this.$store.dispatch('showfoods',item);
				// console.log(1111);
			},
			// 左边菜单列表的better-scroll
			_initMenu(){
				this.menuBS  = new BScroll(this.$refs.menuWrapper,{click:true});			
			},
			// 右边食物清单的better-scroll
			_initFood(){
				this.foodBS  = new BScroll(this.$refs.foodsWrapper,{
					probeType:3,
					click:true
				});	
				this.foodBS.on('scroll',(pos)=>{
                    this.scrollY=Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})		
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);
			},
            _calcHeight(){
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	// console.log(this.listHeight);
            },
		}
    }
	
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 1.1rem;
		width: 100%;
		display: flex;
		overflow: hidden;
		font-size:0.24rem;	
		.menu-wrapper{
			flex:0 0 1.6rem;	
			overflow: hidden;	
			background-color: #eaf5f7; 
			.goods-menu{
              height: 9.4rem;					
			  .menuUl{					
				background-color: #f3f5f7;
				.menuLi{
					width: 100%;
					margin: auto;
					height: 1.08rem;	
					text-align: center;
					display: table;
				&.current {
					background-color: white;
				}		     
					.menuSpan{			
					    border-bottom:0.01rem solid rgba(7,17,27,0.1);				
						color:rgb(20,20,20);
						font-weight: 200;
						font-size:0.24rem;
						line-height: 0.28rem;
						display: table-cell;
						vertical-align: middle;						   
					}	   
				}
			  }
			} 
		}
		.foods-wrapper{	
			flex:1;  	
			overflow: hidden;						
			.goods-foods{			  
				height:78rem;			  			
					.foodsTitle{
						display: inline-block;
						width: 100%;
						height: 0.52rem;
						background-color: #f3f5f7;
						border-left:0.05rem solid #d9dde1;
						color:rgb(147,153,159);
						font-size:0.24rem;
						line-height: 0.52rem;
						padding-left:0.28rem;				
					}
					 .foodsList{
						padding:  0.36rem 0.36rem 0;
					 .foods-content{
						display: flex;      
                        -webkit-flex-direction: row;
						border-bottom:0.01rem solid rgba(7,17,27,0.1);
						padding-bottom: 0.36rem;	
                        .foodsImg{
							width: 1.36rem;
							height: 1.36rem;
						}
						.foodsText{
							padding-left:0.2rem;
                             .text1{
								margin-top:0.04rem ;
								color:rgb(7,17,27);
								font-size:0.28rem;
								line-height: 0.28rem;
								overflow: hidden;
								white-space: nowrap;
								text-overflow: ellipsis;
								margin-bottom: 0.16rem;								
							 }	
							 .text2{
								width: 20%;
							    overflow: hidden;
								white-space: nowrap;
								text-overflow: ellipsis;
								margin-bottom: 0.16rem;
								color:rgb(147,153,159);
								font-size:0.20rem;
								line-height: 0.22rem;
							 }		 					
							.text34{							
								color:rgb(147,153,159);
								font-size:0.20rem;
								line-height: 0.20rem;
								margin-bottom: 0.2rem;		
								.text4{
									margin-left:0.24rem;
									margin-bottom: 0.2rem;
								}
							}
							.textlast{
								display: flex;      
                                justify-content:space-between;
								width: 100%;
								.text56{
                                    flex:0 0 2rem;
									.text5{
										color:rgb(240,20,20);
										font-size:0.28rem;
										line-height: 0.48rem;
										font-weight: 700;
										margin-top:0.16rem;	 
									}
									.text6{
										color:rgb(147,153,159);
										font-size:0.20rem;
										line-height: 0.48rem;
										font-weight: normal;
										margin-left:0.16rem;
										margin-top:0.16rem;
									}
								}
								.text7{
							        flex:1;
								}
						    }
						}
					}
				 }
			}		
	
	    }
	}
</style>


