<template>
	
<div class="menu-wrapper" ref = "menuWrapper">
	<div class="content">
	   <div class="seller-top">	 		   
		 <div>
            <p class="topName">{{seller.name}}</p>
		    <div class="topText">  
               <span class="topText1"><Star :size='"middle"' :score='seller.score'></Star></span>
			   <span class="topText2">({{seller.ratingCount}})</span>
			   <span class="topText3">月售{{seller.sellCount}}单</span>
		    </div>
		 </div>     
         <div class="topCollet" @click="show"> 
			<i class="icon iconfont icon-xin" ref="icon"></i> 
			<p class="iconText" ref="iconText">收藏</P> 
		 </div>	
	   </div>
	   <div class="topLine"></div>
		<div class="topInfo">
			<div class="topInfo1">
				<p class="infotext1">起送价</p>
				<span class="infotext2">{{seller.minPrice}}</span>
				<span class="infotext3">元</span>				
			</div>
			<div class="topInfo2">
				<p class="infotext1">商家配送</p>
				<span class="infotext2">{{seller.deliveryPrice}}</span>
				<span class="infotext3">元</span>			
			</div>
			<div class="topInfo3">
				<p class="infotext1">平均配送时间</p>
				<span class="infotext2">{{seller.deliveryTime}}</span>
				<span class="infotext3">分钟</span>			
			</div>
		</div>
		<div class="middleLine"></div>
		<div class="notice">
			<p class="noticeTitle">公告与活动</p>
			<ul class="noticeUl">
				<li class="noticeText">{{seller.bulletin}}</li>
			</ul>
		</div>
		<div class="noticeList">
			<ul class="noticeUl">		
			    <li  class="noticeLi" v-for="(v,k) in seller.supports" :key="k">
				   <img   class="listImg" :src='imgSrc[v.type].src'>
				   <span  class="listText" v-if="seller.supports" >{{seller.supports[0].description}}</span>
			   </li>
			</ul>
		</div>
		 <div class="middleLine"></div> 
		<div class="notice">
			<p class="noticeTitle">商家实景</p>
	         <div class="seller-wrapper" ref = "sellerWrapper">   
				<ul class="sellImgul">
					<li v-for="(value,k1) in seller.pics" :key="k1">
						<img :src="value" class="sellImgli"> 
					</li>
				</ul>
            </div>
    	</div>
		<div class="middleLine"></div>

		<div class="noticeList">
			<div class="notice title" >
			  <p class="noticeTitle">商家信息</p>
			</div>
			<ul class="noticeUl">		
			    <li  class="noticeLi" v-for="(v,k) in seller.infos" :key="k">
				   <span  class="listText">{{v}}</span>
			   </li>
			</ul>
		</div>


	</div>
	</div>
</template>

<script>
import {mapGetters} from 'vuex'
import Star from '../star/star'
import BScroll from 'better-scroll'

	export default {
		data(){
			return{				
				imgSrc:[
					{type:0,src:'/static/imgs/decrease1.png'},
					{type:1,src:'/static/imgs/discount1.png'},
					{type:2,src:'/static/imgs/guarantee1.png'},
					{type:3,src:'/static/imgs/invoice1.png'},
					{type:4,src:'/static/imgs/special1.png'},
				] ,
				showIcon:true,          
			}
        },
		 computed:{
            ...mapGetters([
                'seller',        
            ])
		 },
		components:{
           Star
        },
	    created(){
			this.$store.dispatch('getgoods');
			this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initSeller();
				},100)
			})
		},
		methods:{
			_initMenu(){
				this.menuBS  = new BScroll(this.$refs.menuWrapper,{click:true});		
			},
			_initSeller(){
				this.sellerBS = new BScroll(this.$refs.sellerWrapper,{click:true,scrollX:true});			
			},
			show(){
				if(this.showIcon){
					this.$refs.icon.style.color="rgb(240,20,20)";
					this.$refs.iconText.innerHTML="已收藏";
					this.showIcon=false;
				}else{
					this.$refs.icon.style.color="rgb(147,153,159)";
					this.$refs.iconText.innerHTML="收藏";
					this.showIcon=true;
				}
			}
		}  
	}	
</script>

<style lang='less' scoped>
.menu-wrapper{
	height: 10rem;
	overflow: hidden;
   .content{
		// height: 20rem;	
   .seller-top{
	 padding:0.36rem;
	 display: flex;   
	 -webkit-flex-direction: row;
	 position: relative;
	 color:rgb(77,85,93);
	 font-size:0.2rem;
	 .topName{
		color:rgb(7,17,27);
		font-size:0.28rem;
		line-height: 0.28rem;
		padding-bottom: 0.16rem;
		font-weight: bold;
	 }
	 .topText{
		display: flex;   
		-webkit-flex-direction: row;
		height: 0.36rem;
		.topText1{
		   line-height: 0.36rem;
		}
		.topText2{
			height:100%;
			margin-right: 0.24rem;
			line-height: 0.36rem;
		}
		.topText3{
			height:100%;
			line-height: 0.36rem;
		}
	 }
	 .topCollet{
		 width: 0.8rem;
		 position: absolute;
		 right:0.36rem;
         .icon-xin{
	     	font-size:0.48rem;
			line-height: 0.48rem;	
		 }
		 .iconText{
			margin-top: 0.08rem;
		    line-height: 0.2rem;
		 }
	  }
	 } 
	 .topLine{
		  border-bottom: 0.01rem solid rgba(7,17,27,0.1);
		  width: 90%;
		  margin: auto;
	 }
	 .topInfo{
		 width: 90%;
		 padding: 0.36rem;
		 display: flex;   
		-webkit-flex-direction: row;
		.topInfo1,.topInfo2 ,.topInfo3{
			width: 30%;
			text-align: center;
			border-right: 0.01rem solid rgba(7,17,27,0.1);
            margin:auto;
			.infotext1,.infotext3{
				color:rgb(147,153,159);
				font-size:0.2rem;
				line-height: 0.2rem;
				margin-bottom: 0.08rem;
			}
			.infotext2{
				color:rgb(7,17,27);
				font-size:0.48rem;
				line-height: 0.48rem;
				font-weight: 200;
			}
			.infotext3{
				color:rgb(7,17,27);
			}
		}
		.topInfo3{
			border:none;
		}	
	 }
	 .middleLine{
		 height: 0.32rem;
		 background-color: #f3f5f7;
		 width: 100%;
	 }
	 .notice{
		 padding: 0.36rem;
		 .noticeTitle{
			//  display: inline-block;
			color:rgb(7,17,27);
			font-size:0.28rem;
			line-height: 0.28rem;
			padding-bottom: 0.16rem;
			font-weight: bold;
		 }
	 }
		.title{
			padding: 0.24rem 0.36rem;		       
			.titleLi{
				color:rgb(7,17,27);
				font-size:0.28rem;
				line-height: 0.28rem;
				// padding-bottom: 0.16rem;
				font-weight: bold;

			}
		}
		.noticeUl{
		   padding: 0 0.24rem;
		   .noticeText{
			    color:rgb(240,20,20);
				font-size:0.24rem;
				line-height: 0.48rem;
				font-weight: 200;
		   }
		   .noticeLi{
			   width: 100%;
			   height: 0.96rem;
			   padding:0.32rem 0.24rem;
			   border-top: 0.01rem solid rgba(7,17,27,0.1);
			   box-sizing:border-box;
               -moz-box-sizing:border-box; 
			   -webkit-box-sizing:border-box;
			   display: table;			  
			   .listImg{
				   height: 0.32rem;	 
			       vertical-align: middle;			   
				   margin-right:0.12rem;
			   }
			   .listText{
				   	font-size:0.24rem;
					line-height: 0.32rem;
					font-weight: 200;
					color:rgb(7,17,27);
			        vertical-align: middle;
			   }
		   } 
		}
		.seller-wrapper{
            width: 5rem;	
			.sellImgul{
				width: 8rem;
				display: flex;   
				-webkit-flex-direction: row;			
				.sellImgli{
					width: 2.4rem;
					height: 1.8rem;
					margin-right: 0.12rem;
				}
			}
		}
	
}	
}
</style>


