<template>
    <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">  
        <div class="foods-wrapper" ref = "foodsWrapper" v-if="foodsflag"> 
            <div class="foods" >
                <div class="imgDiv">
                    <img :src="foods.image" class="Img"> 
                </div> 
                <div class="back" @click="hidefoods">
                     <i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
                </div>                 
                <div class="foodsTop">
                    <div class="foodsLeft">
                        <p class="text1" >{{foods.name}}</p> 
                        <p class="text2">
                            <span class="text3"> 月销售量{{foods.sellCount}}</span>
                            <span class="text4">好评率{{foods.rating}}%</span>
                        </p>
                        <P>
                            <span class="text5" >￥{{foods.price}}</span>
                            <span class="text6" v-show="foods.oldPrice">￥{{foods.oldPrice}}</span>
                        </P>  
                    </div>		  
                     <div class="topRight" v-show="!foods.count" @click="showCart">
                        <div class="shoppCart">加入购物车</div>                  
                    </div> 
                    <div class="Addcut" ><Addcut :food="foods" v-show="foods.count"></Addcut></div>
                </div>
                <div class="middleLine"></div> 
                <div class="foodsCenter">
                        <p class="centerTitle1">商品介绍</p>
                        <div class="centerInfo">{{foods.info}}</div>
                </div>
                <div class="middleLine"></div> 
                <div class="foodsBottom">
                    <p class="centerTitle2">商品评价</p>
                    <div class="elButton">
                        <el-button class="all" @click="All">全部{{foods.ratings.length}}</el-button>
                        <el-button class="recommend" @click="Recommend">推荐{{countnum}}</el-button>
                        <el-button class="complain" @click="Complain">吐槽{{foods.ratings.length-countnum}}</el-button>
                    </div> 
                    <div class="checkRatings">
                       <i class="icon iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
                       <span class="duihaoText">只看有内容的评价</span>
                    </div>          
                </div>   
                <ul class="ratings">
                    <li v-for="(v,k) in ratLists" :key="k" class="ratingsLi" v-show="all || yes === v.rateType">
                        <ul class="ratingsline1">
                            <li>
                                <span class="liText1">{{v.rateTime | date}}</span>
                            </li>
                            <li>
                                <span class="liText2">{{v.username}}</span>
                                <img  class="liImg" :src="v.avatar"> 
                            </li> 
                        </ul>
                        <p class="ratingsline2">
                           <i class="icon iconfont icon-damuzhion " v-if="v.rateType == 0" ></i>
                           <i class="icon iconfont icon-down "    v-else="v.rateType == 1" ></i>
                           <span class="liText3">{{v.text}}</span> 
                        </p>
                    </li>                   
                </ul>
            </div>
        </div>   
    </transition>  
</template>

<script>
import {mapGetters} from  'vuex'
import Vue from 'vue'
import BScroll from 'better-scroll'
import Addcut from '../AddCut/addcut.vue'

export default{
    data(){
        return{
            all: true,
            yes: -1,
            flag: false, //只看有内容的评价的图标设置
            cartflag:true
        }
    },
    components:{
         Addcut,
    },
    computed:{
        ...mapGetters([
            'goods',
            'foodsflag', 
            'foods',       
        ]),
       
        ratLists() {
             //flag为真的时表示对勾图标被选中，此时将只有内容的评论return出去；
            if (this.flag) {
                return this.foods.ratings.filter((item) => {
                    return item.text;
                })
            }
            //如果flag为false，表示对勾图标没被选中，此时将所有的评论return出去；
            return this.foods.ratings;
        },

        //只需计算推荐的评论(Recommend)数量（吐槽的数量等于总数量-推荐数量）
        countnum(){
            // console.log(1)
            return  this.foods.ratings.filter((item) => {
                    return item.rateType === 0
                  }).length
        }
    },  
    //滚动条的设置；
    watch:{
        foods(){
			this.$nextTick(() => {
				setTimeout(() => {
					this._initFoods();
				},100)
			})
        }
    },
	methods:{
        showCart(){
            Vue.set(this.foods,'count',1)      
        },

         //当前页面滚动条的设置；
		_initFoods(){
            setTimeout(() => {
                if(!this.foodsBS){
                    this.foodsBS  = new BScroll(this.$refs.foodsWrapper,{click:true});	
                    return;                   
                }
                this.foodsBS.refresh();
            },100)
        },
        //返回Doogs.vue页面的图标click事件的设置
        hidefoods(){
			this.$store.dispatch('hidefoods');
        },
        //点击查看评论的对勾图标绑定的click事件
        changeFlag(){
            this.flag = !this.flag;
        },
        //三个按钮的点击事件
        Recommend() {
            //推荐按钮
            this.yes = 0;
            this.all = false;
        },
        Complain() {
            //吐槽按钮
            this.yes = 1;
            this.all = false;
        },
        All() {
            //全部
            this.yes = -1;
            this.all = true;
        },
    },
     // 时间的转换：使用过滤器将毫秒转化成YY-MM-DD HH-mm (2017-08-07 12:43)
    filters:{      
        date:function(value){
            function toDouble (n){
                return n<10?'0'+n:n;
            }
            var d = new Date(value);
            return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
        }
    }    
}   
</script>

<style lang="less" scoped>
 .foods-wrapper{
    background-color: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0; 
    bottom: 0;
    z-index:50;
    // height: 15rem;
    overflow: hidden;
    .foods{   
        // height:30rem;
        .imgDiv{
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 100%;
            .Img{
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;  
            }
        }
        .back{
            position: fixed;
            left:0.1rem;
            top:0.1rem;
            color:white;
            padding: 0.1rem;

        }
        .foodsTop{
            position: relative;
            display: flex;
            -webkit-flex-direction: row;
            padding: 0.36rem;
            justify-content :space-between;
                .foodsLeft{
                    padding-left:0.2rem;
                    .text1{
                    margin-top:0.04rem ;
                    color:rgb(7,17,27);
                    font-size:0.28rem;
                    line-height: 0.28rem;
                    font-weight: 700rem;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    margin-bottom: 0.16rem;						
                    }		 					
                    .text2{							
                    color:rgb(147,153,159);
                    font-size:0.20rem;
                    line-height: 0.20rem;
                    margin-bottom: 0.2rem;		
                    }
                    .text4{
                        margin-left:0.24rem;
                        margin-bottom: 0.2rem;
                    }
                    .text5{
                        color:rgb(240,20,20);
                        font-size:0.28rem;
                        line-height: 0.48rem;
                        font-weight: 700;
                        margin-top:0.16rem;       
                    }
                    .text6{
                        color:rgb(147,153,159);
                        font-size:0.20rem;
                        line-height: 0.48rem;
                        font-weight: normal;
                        margin-left:0.16rem;
                        margin-top:0.16rem;
                    }
                }
                .topRight{
                    position: absolute;
                    right:0.36rem;
                    bottom:0.36rem;
                    width: 1.48rem;
                    height: 0.48rem;
                    background-color:rgb(0,160,220);
                    border-radius: 0.24rem;
                    text-align: center;
                    line-height: 0.48rem;
                    .shoppCart{
                        color:rgb(255,255,255);
                        font-size:0.20rem;
                        line-height: 0.48rem;             
                    }
                }
                .Addcut{
                     position: absolute;
                     right:0.36rem;
                    bottom:0.36rem;
                }
        }
        .middleLine{
            height: 0.32rem;
            background-color: #f3f5f7;
            width: 100%;
        }
        .foodsCenter{
            padding: 0.36rem 0.52rem 0.36rem 0.36rem;
            .centerTitle1{
                padding-bottom: 0.12rem;
                font-size:0.28rem;
                line-height: 0.28rem;
                font-weight: 700;
            }
            .centerInfo{
                padding-left: 0.16rem;
                color:rgb(77,85,93);
                font-size:0.24rem;
                line-height: 0.48rem;
                font-weight: 200;
            }
        }
        .foodsBottom{
            padding: 0.36rem 0.36rem 0.24rem;
            border-bottom: 0.01rem solid rgba(7,17,27,0.2);
            .centerTitle2{
                padding-bottom: 0.36rem;
                font-size:0.28rem;
                line-height: 0.28rem;
                font-weight: 700;
            }
            .elButton{
                border-bottom: 0.01rem solid rgba(7,17,27,0.2);
                .all{
                    background-color: rgb(0,160,220);
                    border:0;
                    color:rgb(255,255,255);
                    font-size:0.24rem;
                    line-height: 0.32rem;
                    margin-bottom: 0.36rem;
                }
                .recommend{
                    background-color: rgba(0,160,220,0.2);
                    color:rgb(77,85,93);
					font-size:0.24rem;
                    line-height: 0.32rem;
                    border:0;
                }
                .complain{
                    background-color: rgba(77,85,93,0.2);
                    color:rgb(77,85,93);
					font-size:0.24rem;
                    line-height: 0.32rem;
                    border:0;
                }
            }
            .checkRatings{    
                 padding-top:0.24rem ;
                 .icon-duihao{
                    font-size:0.48rem;
                    line-height: 0.48rem;
                    color:rgb(147,153,159);
                    margin-right: 0.08rem;             
                 }
                .checked {
                    //点击对勾时触发的事件，对勾变成蓝色；
                    color: rgb(0, 160, 220);
                } 
               
                 .duihaoText{
                    font-size:0.24rem;
                    line-height: 0.48rem;
                    color:rgb(147,153,159);
                 }
            }
        }
        .ratings{
            padding:0 0.36rem;
            .ratingsLi{
                padding: 0.32rem 0;
                border-bottom: 0.01rem solid rgba(7,17,27,0.2);
                .ratingsline1{
                    display:flex;
                    justify-content: space-between; 
                    padding-bottom: 0.12rem;               
                    .liText1{
                        font-size:0.20rem;
                        line-height: 0.32rem;
                        color:rgb(147,153,159);
                    }
                    .liText2{
                        font-size:0.20rem;
                        line-height: 0.24rem;
                        color:rgb(147,153,159);
                        margin-right: 0.12rem;
                    }
                    .liImg{
                        width: 0.24rem;
                        height: 0.24rem;
                        border-radius: 0.24rem;                   
                    }
                }
                .icon-damuzhion{
                    font-size:0.24rem;
                    line-height: 0.48rem;
                    color: rgb(0, 160, 220); 
                }
                .icon-down{
                    font-size:0.24rem;
                    line-height: 0.48rem;
                    color:rgb(147,153,159);
                }
                .liText3{
                    font-size:0.24rem;
                    line-height: 0.32rem;
                    color:rgb(7,17,27);  
                }
            }
        }
    }
}
</style>



