<template>
    <div id="app">
        <v-header></v-header>
        <div class="list">
              <div class="goods">
                  <router-link to="/goods">商品</router-link> 
              </div>
              <div class="ratings">
                <router-link to="/ratings">评价</router-link>
              </div>
              <div class="sellers">
                <router-link to="/sellers">商家</router-link>
              </div>  
        </div>        
        <keep-alive>
          <router-view></router-view>
        </keep-alive>          
        <v-footer></v-footer>
       
    </div>
</template>

<script>
  import Header from './components/header/header.vue'
  import Footer from './components/footer/footer.vue' 

  export default { 
    components : {
      'v-header': Header,
      'v-footer':Footer,     
    }
  }
</script>

<style lang='less' scoped>
    .list{
      width: 100%;
      height: 0.8rem;
      border-bottom:0.01rem solid rgba(7,17,27,0.1);
      display: flex;      
      -webkit-flex-direction: row;  
      .goods,.ratings,.sellers{
          font-size: 0.28rem;
          color:rgb(77,85,93);
          font-weight: 700;
          line-height: 0.28rem;
          margin:auto;
          & > a{
              font-size: 0.28rem;
              display: block;
              color:black;
              text-decoration: none;
              &.active{
                color: #e74c3c;
              }
          }
      }
    }
</style>



