import types from '../type.js'
import axios from 'axios'

// 相当于.vue里面的data
const state={
    ratings:[]
};

// 返回数据
const getters={
    ratings(state){
        return state.ratings;
    },
};

// 接收componens里面传过来的方法并提交给mutations执行
const actions={
    getratings({commit}){
        axios.get('../../../static/data.json').then((res)=>{
            if(res.data.errno === 0){
                commit(types.GET_RATINGS,res.data.ratings);
            }
        })
    },
};

// 执行具体动作
const mutations={
    [types.GET_RATINGS](state,data){
       state.ratings=data;
    },

};
export default{
    state,
    getters,
    actions,
    mutations
}


