<template>
 <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
    <div class="detail" v-show="detailflag">
        <p class="title">{{seller.name}}</p>
        <div class="starimg" >
             <Star :score='seller.score'></Star> 
        </div>
        <div class="line">
            <div class="line1"></div>
            <span class="linetext">优惠信息</span>
            <div class="line2"></div>
        </div>
        <ul class="discoun">
            <li class="discounLi" v-for="(v,k) in seller.supports" :key="k">
                <img  class="discouImg" :src='imgSrc[v.type].src' >
                <span class="discouText">{{v.description}}</span>
            </li>          
        </ul>
        <div class="line">
            <div class="line1"></div>
            <span class="linetext">商家公告</span>
            <div class="line2"></div>
        </div>
        <div class="discoun">
            <div class="discounDiv">{{seller.bulletin}}</div>          
        </div>
        <div class="closeIcon">
             <i class="icon iconfont icon-X" @click="close" ></i>          
        </div>
    </div>
  </transition>
</template>

<script>
import {mapGetters} from  'vuex'
import Star from '../star/star'


export default{
    data(){
      return{
          imgSrc:[
            {type:0,src:'/static/imgs/decrease.png'},
            {type:1,src:'/static/imgs/discount.png'},
            {type:2,src:'/static/imgs/guarantee.png'},
            {type:3,src:'/static/imgs/invoice.png'},
            {type:4,src:'/static/imgs/special.png'},
          ]           
      }
    },
    computed:{
        ...mapGetters([
            'seller',
            'detailflag',      
        ])
    }, 
    methods:{
        close(){
            return this.$store.dispatch('closedetail')
        }
    } ,
    components:{
        Star
    }  
}

    
</script>
<style lang="less" scoped>
   .detail{
       position: fixed;
       height: 100%;
       top:0;
       left:0;
       right:0;
       bottom:0;
       background-color: rgba(7, 17, 28, 0.8);
       z-index:100;
    .title{
        margin-top:1.28rem;
        font-size:0.32rem;
        line-height: 0.32rem;
        text-align: center;
        font-weight: 700;
        color:rgb(255, 255, 255);
    }
    .starimg{
        margin-top:0.32rem;
        height: 0.48rem;
        text-align: center;
    }   
    .line{
        display: flex;      
        -webkit-flex-direction: row; 
        height: 0.28rem;  
        padding: 0 0.72rem 0 0.72rem;
        margin-top:0.56rem;  
        .line1,.line2{      
            width: 2.24rem;
            height: 0.035rem;
            background-color:rgba(255,255,255,0.2);      
            margin:auto; 
        }   
        .linetext{
            font-size: 0.26rem;
            color:rgb(255,255,255);
            font-weight: 700;
            line-height: 0.28rem;
            margin:0 0.24rem;  
        }
    }
    .discoun{
        padding:0 0.96rem;
        padding-top: 0.24rem;
        .discounDiv{
            margin-top: 0.24rem;
            font-size: 0.24rem;
            font-weight: 200;
            color:rgb(255, 255, 255);
            line-height: 0.48rem;         
        } 
        .discounLi{
            margin-top: 0.24rem;
            .discouImg{           
                height: 0.32rem;
                vertical-align: top;         
            }
            .discouText{
                margin-left:0.12rem;
                font-size: 0.24rem;
                font-weight: 200;
                color:rgb(255, 255, 255);
                line-height: 0.32rem;         
            }
        }     
    }
    .closeIcon{
        width: 100%;
        position: absolute;
        bottom:0.64rem;
        text-align: center;
        .icon-X{      
            font-size: 0.64rem;      
            color:rgba(255, 255, 255,0.5); 
        }
     }
  }   
</style>



