<template>
    <div>
        <div class="buydetail"  >
            <div class="buyTop" v-show="hideShopp"></div>
             <transition enter-active-class="animated slideInUp" leave-active-class="animated slideOutDown"> 
                <div class="buyBottom" v-show="hideShopp">
                    <div class="nav">
                        <div class="navleft">购物车</div>
                        <div class="navRight" @click="cleanEmpty">清空</div>
                    </div>
                    <div class='shopp-wrapper' ref = "shoppWrapper"> 
                        <div class="shoppList">
                            <ul class="listUl">
                                <li v-for="(item,k) in  selectFoods" :key="k" class="listLi">
                                    <p class="listLeft">{{item.name}}</p>
                                    <P class="listRight">
                                        <p class="listRight1">￥{{item.price}}</p>
                                        <P class="listRight2"><Addcut :food="item"></Addcut></P>
                                    </P>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
             </transition> 
        </div>
        <div class="footer">
            <div class="content"> 
                <div class="footerLeft">
                    <div class="shoppCart">
                        <div class="shopplogo" :class='{"showlogo":totalCount>0}' @click="showshopp">
                        <i class="icon iconfont icon-gouwuche"></i>
                        </div>
                        <div class="totalCount" v-show="totalCount"><span class="countSpan">{{totalCount}}</span></div>
                    </div>
                    <div class="price"><span class="priceSpan">￥{{totalPrice}}</span></div>
                    <div class="deliveryPrice">另需配送费￥4元</div>    
                </div>
                <div class="footerRight" @click.stop.prevent="pay">
                <div class="minPrice" :class='{"enough":totalPrice>=seller.minPrice}'>{{payPrice}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {mapGetters} from 'vuex'
    import Addcut from '../AddCut/addcut.vue'
    import BScroll from 'better-scroll'
   
    export default {
        data(){
            return{
               shoppflag:false,
            }
        },
        components:{
            Addcut,
        },
        methods:{
            showshopp(){
                if( !this.totalCount ){
                    return;
                }
                this.shoppflag = !this.shoppflag;
            },         
            pay(){
                alert("确定提交订单？");
            },
            cleanEmpty(){
                this.selectFoods.forEach(food => {
                   return  food.count=0;    
                });
                this.shoppflag =false;
            }
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods',
            ]),
            selectFoods(){
                let foods=[];
                this.goods.forEach(good=> {
                    good.foods.forEach(food=>{
                         if(food.count>0){
                             foods.push(food);
                         }
                    })
                });
                return foods;
            },
            totalCount(){
                let count=0;
                this.selectFoods.forEach(food=>{
                    count+=food.count;
                })
                return count;
            },
            totalPrice(){
                let price=0;
                this.selectFoods.forEach(food=>{
                    price+=food.count*food.price;
                })
                return price;
            },
            payPrice(){
                if(this.totalPrice ===0 ){
                    return `￥${this.seller.minPrice}起送`;
                }else if(this.totalPrice<this.seller.minPrice){   
                    return `还差${this.seller.minPrice-this.totalPrice}起送`;
                }else{
                    return `去结算`;
                }
            },
            hideShopp(){
                if(this.totalCount==0){
                    this.shoppflag = true;
                }
                if( !this.totalCount ){
                    return;
                }
                let show = !this.shoppflag;
                if( show ){
                    this.$nextTick( () => {
                        if( !this.shoppBS ){
                            this.shoppBS = new BScroll(this.$refs.shoppWrapper,{
                                click:true
                             });
                        }else{
                            this.shoppBS.refresh();
                        }
                    });
                }
                    return show ;
            },
        },
        
        
    }
    
</script>
<style lang="less" scoped>
.buydetail{
    position: fixed;
    bottom:1.1rem;
    z-index:90;    
    width:100%;     
    .buyTop{  
        background-color: rgba(7, 17, 28, 0.8);          
        height: 30rem;  
    }
    .buyBottom{    
        // height: 6rem;
        background-color: white;
        // max-height: 4.9rem;
        overflow: hidden;
        .nav{            
            padding: 0 0.36rem; 
            height: 0.8rem;   
            display: flex;
            line-height: 0.8rem;
            justify-content: space-between;
            border-bottom:0.01rem solid rgba(7, 17, 27, 0.1);
            .navleft{
                font-size:0.28rem;
                font-weight: 200;
                color:rgb(7, 17, 27);
            }
            .navRight{
                font-size:0.24rem;
                font-weight: 200;
                color:rgb(0, 160, 220); 
            }  
        }
        .shopp-wrapper{
            max-height: 4.3rem;
            overflow: hidden;
            .shoppList{
                max-height: 20rem;
                overflow: hidden;
                .listUl{
                    padding:0 0.36rem;
                    .listLi{
                        padding: 0.24rem 0;
                        height:0.96rem;
                        border-bottom:0.01rem solid rgba(7, 17, 27, 0.1);
                        display: flex;
                        justify-content: space-between;
                        // line-height: 0.48rem;
                        box-sizing: border-box;
                        .listLeft{
                            font-size:0.28rem;
                            line-height: 0.48rem;
                            color:rgb(7, 17, 27); 
                        }
                        .listRight{
                            display: flex;
                            justify-content: space-between;
                            .listRight1{
                                color:rgb(240,20,20);
                                font-size:0.28rem;
                                line-height: 0.48rem;
                                font-weight: 700;
                                margin-right: 0.24rem;
                            } 
                        }
                    }
                }
            }
        }
    }
}
.footer{
    height: 1.1rem;
    width: 100%;
    position:fixed;
    bottom: 0;
    left: 0;
    color:white;
    z-index: 90;
    .content{
        display: flex;
        .footerLeft{
            flex:1;
            background: #141d27;
            display: flex;      
            -webkit-flex-direction: row;
            .shoppCart{
                width:1rem;
                height: 1rem;
                padding: 0.12rem;
                border-radius:1rem;
                margin-left: 0.24rem;
                position: relative;
                bottom:0.25rem; 
                background: #141d27;            
                .shopplogo{
                    display: inline-block;
                    background:#2b333b;
                    width: 100%;
                    height: 100%;
                    border-radius:1rem;
                    text-align: center;
                    line-height: 1rem;
                    &.showlogo{
                        background: rgb(0,160,220);
                        color:white;
                    }
                    &.not-showlogo{
                        background:#2b333b;
                         color:rgba(225,225,225,0.4);
                    }
                    .icon-gouwuche{                  
                        font-size:0.48rem;
                        line-height: 0.48rem;
                    }
                }
                .totalCount{
                    position:absolute;
                    bottom:0.85rem;
                    left:0.8rem;
                    width: 0.48rem;
                    height: 0.35rem;
                    border-radius: 0.2rem;
                    background-color: rgb(240,20,20);
                    box-shadow: 0rem 0.04rem 0.08rem 0 rgba(0,0,0,0.4);
                    text-align: center;
                    .countSpan{
                        color:rgb(225,225,225);
                        font-size:0.18rem;
                        line-height: 0.32rem;
                        font-weight: 700;
                        line-height: 0.35rem;
                    }
                }
           }
            .price{
                padding:0.24rem;
                .priceSpan{ 
                    color:rgba(225,225,225,0.4);
                    font-size:0.32rem;
                    line-height: 0.48rem;
                    font-weight: 700;
                    padding-right: 0.24rem;   
                    border-right:0.01rem solid rgba(225,225,225,0.1);
                }
            }
            .deliveryPrice{
                padding: 0.24rem 0;
                color:rgba(225,225,225,0.4);
                font-size:0.25rem;
                line-height: 0.48rem;
            }
        }
        .footerRight{
            flex:0 0 2.1rem;
            background: #2b333b;         
            text-align: center;
            .minPrice{
                padding: 0.24rem 0;
                width: 100%;
                height: 100%;
                display: inline-block;
                color:rgba(225,225,225,0.4);
                font-size:0.24rem;
                line-height: 0.62rem;
                font-weight: 700;
                &.enough{
                    background: rgb(0,160,220);
                    color: #fff;
                }
                &.not-enough{
                    background: #2b333b;
                }
            }
        }
    }
}

</style>



