import types from '../type.js'
import axios from 'axios'

// 相当于.vue里面的data
const state={
    goods:[],
    foodsflag:false,
    foods:[],

};

// 返回数据
const getters={
    goods(state){
        return state.goods;
    },
    foodsflag(state){
        return state.foodsflag;
    },
    foods(state){
        return state.foods;
    },

};

// 接收componens里面传过来的方法并提交给mutations执行
const actions={
    getgoods({commit}){
        axios.get('../../../static/data.json').then((res)=>{
            if(res.data.errno === 0){
                commit(types.GET_GOODS,res.data.goods);
            }
        })
    },
    showfoods({commit},item){
        commit(types.SHOW_FOODS,item); 
        // 使用常量名称告诉mutations    
    },
    hidefoods({commit},item){
        commit(types.HIDE_FOODS,item); 
        // 使用常量名称告诉mutations    
    },
    
    
};

// 执行具体动作
const mutations={
    [types.GET_GOODS](state,data){
       state.goods=data;
    },
    [types.SHOW_FOODS](state,item){
       state.foodsflag=true;
       state.foods=item;
   },
    [types.HIDE_FOODS](state,item){
       state.foodsflag=false;
    //    state.foods=item;
   },


};
export default{
    state,
    getters,
    actions,
    mutations
}


