import types from '../type.js'
import axios from 'axios'

// 相当于.vue里面的data
const state={
    seller:{},
    detailflag:false
};

// 返回数据
const getters={
    seller(state){
        return state.seller;
    },
    detailflag(state){
        return state.detailflag;
    }
};

// 接收componens里面传过来的方法并提交给mutations执行
const actions={
    getseller({commit}){
        axios.get('../../../static/data.json').then((res)=>{
            if(res.data.errno === 0){
                commit(types.GET_SELLER,res.data.seller);
            }
        })
    },
    // 接收header里面传过来的显示详情的方法并提交给mutations
    getdetail({commit}){
        commit(types.SHOW_DETAIL) 
        // 使用常量名称告诉mutations    
    },
    closedetail({commit}){
        commit(types.HIDE_DETAIL)
    }

};

// 执行具体动作
const mutations={
    [types.GET_SELLER](state,data){
       state.seller=data;
    },
    // 改变state里面detailshow的值，显示详情页
   [types.SHOW_DETAIL](state){
       state.detailflag=true;
   },
    [types.HIDE_DETAIL](state){
        state.detailflag=false;
    }

};
export default{
    state,
    getters,
    actions,
    mutations
}


