<template>
	<div class="ratings">
		<div class="ratingsTop">
			<div class="top-left">
				<div class="leftLine">
					<P class="leftScore1">{{seller.score}}</P>
					<P class="leftScore2">综合评分</P>
					<P class="leftRankRate">高于周边商家{{seller.rankRate}}%</P>
				</div>
			</div>
			<ul class="top-right">
				<li class="rightScore1">
					<span class="scoreText1">服务态度</span>
					<Star :size='"small"' :score='seller.score'></Star>
					<span class="scoreText2">{{seller.score}}</span>
				</li>
				<li class="rightScore2">
					<span class="scoreText1">食物评分</span>
					<Star :size='"small"' :score='seller.foodScore'></Star>
					<span class="scoreText2">{{seller.foodScore}}</span>
				</li>
				<li class="rightTime">
					<span class="scoreText1">送达时间</span>
					<span class="scoreText3">{{seller.deliveryTime}}分钟</span>
				</li>
			</ul>
		</div>
		<div class="middleLine"></div>
		<div class="foodsBottom">
			<div class="elButton">
				<el-button class="all" @click="All">全部{{ratings.length}}</el-button>
				<el-button class="recommend" @click="Recommend">满意{{countnum}}</el-button>
				<el-button class="complain" @click="Complain">不满意{{ratings.length-countnum}}</el-button>
			</div>
			<div class="checkRatings">
				<i class="icon iconfont icon-duihao" :class="{'checked':flag}" @click="changeFlag"></i>
				<span class="duihaoText">只看有内容的评价</span>
			</div>
		</div>
		<div class="menu-wrapper" ref = "menuWrapper">
			<div class="ratingsList">
				<div v-for="(v,k) in ratLists" :key="k" class="ratingsLi" v-show="all || yes === v.rateType">
					<ul class="ratingsUl">
						<li class="ratingsLi1">
                            <img class="imgHeader" :src="v.avatar">
							<div>
								<p class="liText1">{{v.username}}</p>
								<p class="starText2">
									<Star :size='"minimum"' :score='seller.score'></Star>
									<span class="liText2" v-show="v.deliveryTime">{{v.deliveryTime}}分钟送达</span>
								</p>
							</div>
						</li>						
						<li>
							<span class="liText3">{{v.rateTime | date}}</span>	
						</li>
					</ul>
					<div class="ratingsText">{{v.text}}</div>
					<div class="ratingsline2">
						<i class="icon iconfont icon-damuzhion " v-if="v.rateType == 0"></i>
						<i class="icon iconfont icon-down " v-else="v.rateType == 1"></i>
						<div v-for="(vr,i) in v.recommend" :key="i" class="ratingsRecom">
						   <div class="recomDiv"><span class="recomSpan">{{vr}}</span></div>
						</div>
					</div>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
import { mapGetters } from 'vuex'
import BScroll from 'better-scroll'
import Star from '../star/star'
export default {
	data() {
		return {
			all: true,
			yes: -1,
			flag: false //只看有内容的评价的图标设置
		}
	},
	// 时间的转换：使用过滤器将毫秒转化成YY-MM-DD HH-mm (2017-08-07 12:43)
	filters: {
		date: function (value) {
			function toDouble(n) {
				return n < 10 ? '0' + n : n;
			}
			var d = new Date(value);
			return d.getFullYear() + "-" + toDouble(d.getMonth() + 1) + "-" + toDouble(d.getDate()) + " " + toDouble(d.getHours()) + ":" + toDouble(d.getMinutes());
		}
	},
	computed: {
		...mapGetters([
			'ratings',
			'seller',
		]),
		ratLists() {
			// console.log(this.ratings)
			//flag为真的时表示对勾图标被选中，此时将只有内容的评论return出去；
			if (this.flag) {
				console.log(this.ratings)
				return this.ratings.filter((item) => {
					return item.text;
				})
			}
			//如果flag为false，表示对勾图标没被选中，此时将所有的评论return出去；
			return this.ratings;
		},

		//只需计算推荐的评论(Recommend)数量（吐槽的数量等于总数量-推荐数量）
		countnum(){
			// console.log(1)
			return  this.ratings.filter((item) => {
				return  item.rateType === 0
			}).length
		}
	},
	components: {
		Star
	},
	created() {
		this.$store.dispatch('getratings');
		this.$store.dispatch('getseller');
		this.$nextTick(() => {
			setTimeout(() => {
				this._initMenu();
			}, 100)
        })
	},
	methods: {
		//点击查看评论的对勾图标绑定的click事件
		changeFlag() {
			this.flag = !this.flag;
			this.refresh()
		},
		refresh() {
			setTimeout(() => {
				this.menuBS.refresh()
			}, 50)
		},
		_initMenu() {
			this.menuBS = new BScroll(this.$refs.menuWrapper);
		},
		//三个按钮的点击事件
		Recommend() {
			//推荐按钮
			this.yes = 0;
			this.all = false;
			this.refresh()
		},
		Complain() {
			//吐槽按钮
			this.yes = 1;
			this.all = false;
			this.refresh()
		},
		All() {
			//全部
			this.yes = -1;
			this.all = true;
			this.refresh()
		},
	},
}	
</script>

<style lang='less' scoped>
.ratings {
	.ratingsTop {
		display: flex;
		.top-left {
			width: 2.75rem;
			padding: 0.36rem 0;
			text-align: center;
			.leftLine {
				border-right: 0.01rem solid rgba(7, 17, 27, 0.1);
				.leftScore1 {
					color: rgb(255, 153, 0);
					font-size: 0.48rem;
					line-height: 0.56rem;
					margin-bottom: 0.12rem;
				}
				.leftScore2 {
					color: rgb(7, 17, 27);
					font-size: 0.24rem;
					line-height: 0.24rem;
					margin-bottom: 0.16rem;
				}
				.leftRankRate {
					color: rgb(147, 153, 159);
					font-size: 0.2rem;
					line-height: 0.2rem;
					margin-bottom: 0.12rem;
				}
			}
		}
		.top-right {
			flex: 1;
			padding: 0.36rem 0.48rem;
			.rightScore1 {
				display: flex;
				-webkit-flex-direction: row;
			}
			.rightScore2 {
				display: flex;
				-webkit-flex-direction: row;
			}
			.rightTime {
				display: flex;
				-webkit-flex-direction: row;
			}
			.scoreText1 {
				color: rgb(7, 17, 27);
				font-size: 0.24rem;
				line-height: 0.32rem;
				display: inline-block;
				margin-right: 0.24rem;
			}
			.scoreText2 {
				color: rgb(255, 153, 0);
				font-size: 0.24rem;
				line-height: 0.36rem;
				display: inline-block;
				margin-left: 0.24rem;
			}
			.scoreText3 {
				color: rgb(147, 153, 159);
				font-size: 0.24rem;
				line-height: 0.36rem;
				display: inline-block;
			}
		}
	}
	.middleLine {
		height: 0.32rem;
		background-color: #f3f5f7;
		width: 100%;
	}
	.foodsBottom {
		padding: 0.36rem 0.36rem 0.24rem;
		border-bottom: 0.01rem solid rgba(7, 17, 27, 0.2);
		.elButton {
			border-bottom: 0.01rem solid rgba(7, 17, 27, 0.2);
			.all {
				background-color: rgb(0, 160, 220);
				border: 0;
				color: rgb(255, 255, 255);
				font-size: 0.24rem;
				line-height: 0.32rem;
				margin-bottom: 0.36rem;
			}
			.recommend {
				background-color: rgba(0, 160, 220, 0.2);
				color: rgb(77, 85, 93);
				font-size: 0.24rem;
				line-height: 0.32rem;
				border: 0;
			}
			.complain {
				background-color: rgba(77, 85, 93, 0.2);
				color: rgb(77, 85, 93);
				font-size: 0.24rem;
				line-height: 0.32rem;
				border: 0;
			}
		}
		.checkRatings {
			padding-top: 0.24rem;
			.icon-duihao {
				font-size: 0.48rem;
				line-height: 0.48rem;
				color: rgb(147, 153, 159);
				margin-right: 0.08rem;
			}
			.checked {
				//点击对勾时触发的事件，对勾变成蓝色；
				color: rgb(0, 160, 220);
			}
			.duihaoText {
				font-size: 0.24rem;
				line-height: 0.48rem;
				color: rgb(147, 153, 159);
			}
		}
	}
	.menu-wrapper{
       height: 3.3rem;
	   overflow: hidden;   
		.ratingsList {
			padding: 0 0.36rem;
			.ratingsLi {
				border-bottom: 0.01rem solid rgba(7, 17, 27, 0.2);
				.ratingsUl {
					display: flex;
					justify-content: space-between;
					padding:0.36rem 0 0 0.12rem ;

					.ratingsLi1{
                        display: flex;
					    justify-content: space-between;
						.imgHeader {
							width: 0.56rem;
							height: 0.56rem;
							border-radius: 0.56rem;
							margin-right: 0.24rem;
						}
						.liText1 {
							font-size: 0.20rem;
							line-height: 0.24rem;
							color: rgb(7, 17, 27);
							font-weight: 700;
						}
						.starText2{
							display: flex;
							justify-content: space-between;
							display: table;
							.liText2 {
								display: table-cell;
							    vertical-align: middle;
								font-size: 0.20rem;
								line-height: 0.24rem;
							    color: rgb(147, 153, 159);
								font-weight: 200;
								margin-left: 0.12rem;
						    }
						}
						.liText3 {
							font-size: 0.20rem;
							line-height: 0.24rem;
							color: rgb(147, 153, 159);
							font-weight: 200;
						}
				    }	
				}
				.ratingsText{
					font-size: 0.24rem;
					line-height: 0.36rem;
					color: rgb(7, 17, 27);
					padding-left: 0.8rem;
					font-weight:500;
					margin-bottom: 0.16rem;
				}
				.ratingsline2{
					padding: 0 0 0.36rem 0.8rem;
				    display: flex;
                    -webkit-flex-direction: row;
					.icon-damuzhion {
						font-size: 0.24rem;
						line-height: 0.48rem;
						color: rgb(0, 160, 220);
					}
					.icon-down {
						font-size: 0.24rem;
						line-height: 0.48rem;
						color: rgb(147, 153, 159);
					}
					.ratingsRecom{
                         flex:1;
						 overflow: hidden;
						.recomDiv {
							width: 1rem;
							border: 0.01rem solid rgba(7, 17, 27, 0.2);
							background-color: rgb(255,255,255);
							margin-left: 0.16rem;
							text-overflow: ellipsis;
							white-space: nowrap;
							overflow: hidden;
							border-radius: 0.02rem;
							padding:0.08rem ;
							.recomSpan{
								font-size: 0.24rem;
								line-height: 0.32rem;
								color: rgb(7, 17, 27);

							}
						}
					}

				}
			}
		}
	}
}
</style>


