<template>
    <div class="addcut">
        <transition name="move">
            <div class="reduce" @click.stop="reduce" v-show="food.count"><i class="icon iconfont icon-jianhao"></i></div>
        </transition> 
        <div class="countNum" v-show="food.count">{{food.count}}</div>        
        <div class="add" @click.stop="add"><i class="icon iconfont icon-jiahao"></i></div>        
    </div>
</template>
<script>
    import Vue from 'vue' 
    import {mapGetters} from 'vuex'  
    export default{
        props:['food'], //在Goods.vue中引入的模板<Addcut :food="item"></Addcut>定义一个属性food承接item;
                        //用props将数据传递过来
        
        methods:{
            //点击加号时触碰的事件；
            add(){
                //点击+号时，判断count属性有没有注册到food里面，如果没有注册，注册count属性到food数组里，默认值为1；
                if(!this.food.count){
                    Vue.set(this.food , 'count' ,1);
                }else{
                    this.food.count++;
                    //如果已经注册了，就让count加1
                }
            },
            //点击减号时触碰的事件；
            reduce(){
                if(this.food.count){
                    this.food.count--;
                }
            }
        }
   }
    
</script>
<style lang="less" scoped>
.addcut{
    width: 1.3rem;
    display: flex;   
    justify-content: center;   
    position:relative; 
    height: 0.5rem;
    .move-enter-active{
        transition: all 0.5s;
    }
    .move-leave-active {
        transition: all 0.5s;
        transform:translate(0.3rem) rotate(90deg);
    }
    //count>0时候的动画，v-show显示时的动画效果
    .move-enter, .move-leave-active {
        transform:translate(0.2rem) rotate(90deg);
        opacity: 0
    }
    .reduce{     
        position: absolute;
        left:0;
        bottom:0;  
        .icon-jianhao{
            color:rgb(0,160,220);
            font-size:0.48rem;
            line-height: 0.48rem;
        }
    }
    .countNum{
        width: 0.32rem;
        position: absolute;
        color:rgb(147,153,159);
        font-size:0.20rem;
        line-height: 0.48rem;
        text-align: center;
        bottom:0;
    }
    .add{
        position: absolute;
        right:0;
        bottom:0;
        .icon-jiahao{
            color:rgb(0,160,220);
            font-size:0.48rem;
            line-height: 0.48rem;
        }
    }  
}   

</style>



