<template>
   <div class="star" :class="sizeType">
        <span v-for="active in itemlist" class="starSpan" :class="active"></span>     
   </div>
</template>
<script>
const LENGTH=5;
const ON="on";
const HALF='half';
const OFF='off';

export default{
    props:['score' ,'size'],
    computed:{
        sizeType(){
            return this.size;
        },
        itemlist(){
            let result=[];
            let integer=Math.floor(this.score);
            let decimal=this.score-integer;
            for(let i=0;i<integer;i++){
                result.push(ON);              
            }
            if(decimal>=0.5){
                result.push(HALF);
            }
            while(result.length<LENGTH){
                result.push(OFF);
            }
            return result;
        }
    }
}

    
</script>
<style lang="less" scoped>
   .star{      
       font-size: 0.5rem;
	   &.minimum{
           .starSpan{
            display: inline-block;
			width: 0.3rem;
			height: 0.3rem;
			margin-right:0;
			&.on{
				background-image: url('./imgs/star_on.png');
				background-size: 0.2rem 0.2rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('./imgs/star_half.png');
				background-size: 0.2rem 0.2rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('./imgs/star_off.png');
				background-size: 0.2rem 0.2rem;
				background-repeat: no-repeat;
			}
          }      
       }
	   &.small{
           .starSpan{
            display: inline-block;
			width: 0.4rem;
			height: 0.4rem;
			margin-right:0;
			&.on{
				background-image: url('./imgs/star_on.png');
				background-size: 0.3rem 0.3rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('./imgs/star_half.png');
				background-size: 0.3rem 0.3rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('./imgs/star_off.png');
				background-size: 0.3rem 0.3rem;
				background-repeat: no-repeat;
			}
          }      
       }
       &.middle{
           .starSpan{
            display: inline-block;
			width: 0.5rem;
			height: 0.5rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('./imgs/star_on.png');
				background-size: 0.36rem 0.36rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('./imgs/star_half.png');
				background-size: 0.36rem 0.36rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('./imgs/star_off.png');
				background-size: 0.36rem 0.36rem;
				background-repeat: no-repeat;
			}
          }      
       }
       .starSpan{
            display: inline-block;
			width: 0.5rem;
			height: 0.5rem;
			margin-right: 0.1rem;
			&.on{
				background-image: url('./imgs/star_on.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
			&.half{
				background-image: url('./imgs/star_half.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
			&.off{
				background-image: url('./imgs/star_off.png');
				background-size: 0.5rem 0.5rem;
				background-repeat: no-repeat;
			}
       }     
   }

</style>



